.bottom-wrap {
  position: fixed;
  bottom: 0px;
  background-color: #f3f3f3;
  border-top: 1px solid #e9e9e9;
  left: 0px;
  display: flex;
  justify-content: space-around;
  width: 100%;
  height: 50px;
  div {
    line-height: 50px;
    font-size: 12px;
  }
  .checked{
    color: #f00;
  }
}
